import 'rrweb/dist/rrweb.min.css'
import { Replayer } from 'rrweb'

const start = async () => {
  const url = window.location.hash.substring(1)
  console.log(url)
  if (!url) {
    document.body.innerHTML = `Append "#http://url/of/rrweb_events.json" to the url of this page`
    return
  }
  const req = await fetch(url)
  const responseTxt = await req.text()
  const response = JSON.parse(responseTxt)
  console.log(response)
  const player = new Replayer(response.events, {
    speed: 1,
    root: document.body,
  })
  player.play()
}

start()